@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: 320px;
  max-width: 1150px;
  background: #ffffff;
  font-family: "Open Sans", Arial, sans-serif;
  color: #000000;
  padding: 0;
  margin: 0 auto;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
a {
  text-decoration: none;
}
button,
input,
select,
textarea {
  font-family: inherit;
}
.title-h2 {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .title-h2 {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title-h2 {
    font-size: 53px;
    line-height: 57px;
  }
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.btn {
  padding-top: 14px;
  padding-bottom: 14px;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
