  background-color: #ffffff;
  color: #444444;
  text-decoration: none;
}
.btn--modal {
  background-color: #63d1bb;
  border: none;
  color: #ffffff;
}
.btn:hover {
  background-color: #444444;
  color: #ffffff;
}
.btn:active,
.btn:focus {
  color: #7c7c7c;
}
